*{
    margin-top: 0px;
    padding: 0;
}

table,td{
    border:1px solid black;
    text-align: center;
    border-collapse: collapse;
}
table{
    margin-top: 60px;
}

td{
   width: 65px;
   height:60px;
   
}
.path td{
    border-left: 1px solid white;
    border-right: 1px solid white;
    border-bottom: 1px solid white;
}

.path2 td{
    border: 1px solid white;
}
.path3 td{
    border-left: 1px solid white;
    border-right: 1px solid white;
    border-bottom: 1px solid black;
}

button{
    width: 35px;
    height: 35px;
    line-height: 35px;
    text-align: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: bold;
    font-size: 19px;
}
.link{
    text-decoration: none;
    font-size:18px;
    color: black;

}
.color1{
    background-color: greenyellow;

}
.color2{
    background-color: red;

}


@media screen and (max-width: 1024px) and (min-width:425px) {
    table {
       margin-left: 33px;
       margin-top: 40px;
    }

  .tablet{
     display: flex;
     width:500px;
     margin-top: 20px;
     justify-content: space-evenly;
     margin-right: 111px;
     margin-left:40px;
    
  }
  .tablet .btn{
    width: 60px;
    border-radius:8px;
    border: 1px solid darkcyan;
  }

}
.mobile{
    display: flex;
    justify-content: space-evenly;
    width: 400px;
    padding-top: 20px;
}